@import (less) 'color.less';
.navigation-header-container {
    margin-right: -40px;
    margin-left: -40px;
}

.sticky-default {
    position: fixed !important;
    left: 0 !important;
    z-index: 9999 !important;
}

#body-container.other-header ::ng-deep nbp-navigation-header .header-overlay.sticky {
    top: 4.375em !important;
}

#body-container.abc-header ::ng-deep nbp-navigation-header .header-overlay.sticky {
    top: 3.125em !important;
}

#body-container.other-header ::ng-deep cj-wizard nbp-progress .row.nbp-progress-container {
    .sticky-default;
    top: 6.6875em !important;
    width: 100%;
    margin: 0 !important;
}

#body-container.abc-header ::ng-deep cj-wizard nbp-progress .row.nbp-progress-container {
    .sticky-default;
    top: 5.4375em !important;
    width: 100%;
    margin: 0 !important;
}

#body-container.other-header #body-content.nav-header-slim ::ng-deep cj-wizard #wizard-content {
    padding-top: 10.75em !important;
}

#body-container.abc-header #body-content.nav-header-slim ::ng-deep cj-wizard #wizard-content {
    padding-top: 9.5em !important;
}

#body-container nbp-navigation-header.nav-header-slim ::ng-deep #navigation-header-title {
    border-bottom: none !important;
    //vertical-align: bottom !important;
    height: 2em !important;
    padding-top: .3125em !important;
}

#body-container #body-content.nav-header-slim ::ng-deep cj-wizard #progress-container {
    border-top: none !important;
}

#body-container #body-content.nav-header-slim ::ng-deep cj-wizard nbp-progress #step-container {
    height: 3.125em !important;
    //vertical-align: top !important;
}

#body-container.other-header #body-content:not(.nav-header-slim) {
    padding-top: 9.0625em !important;
}

#body-container.abc-header #body-content:not(.nav-header-slim) {
    padding-top: 7.8125em !important;
}